import { Component, computed, signal } from '@angular/core';
import { NgFor, NgIf } from '@angular/common';
import { ToolCardComponent } from '../../components/tool-card/tool-card.component';
import { FiltersComponent } from '../../components/filters/filters.component';
import { ToolService } from '../../services/tool.service';
import { RouterLink } from '@angular/router';

@Component({
  standalone: true,
  selector: 'app-tools',
  imports: [NgFor, NgIf, ToolCardComponent, FiltersComponent, RouterLink],
  template: `
    <h1 class="text-2xl font-bold mb-4">Browse Tools</h1>
    <app-filters (changed)="onFilters($event)"></app-filters>
    <div class="mt-6 grid-auto">
      <ng-container *ngIf="filtered().length; else empty">
        <app-tool-card *ngFor="let tool of filtered()" [tool]="tool" [isCompared]="isCompared(tool.id)" />
      </ng-container>
      <ng-template #empty>
        <div class="card">No tools match your filters.</div>
      </ng-template>
    </div>

    <!-- Floating Compare Bar -->
    <div *ngIf="compareTools().length" class="fixed bottom-4 left-1/2 -translate-x-1/2 z-50 bg-white rounded-2xl shadow-lg ring-1 ring-brand-200 px-6 py-3 flex items-center gap-4 animate-fade-in">
      <div class="flex items-center gap-2">
        <ng-container *ngFor="let t of compareTools()">
          <div class="flex flex-col items-center">
            <img [src]="t.logo" [alt]="t.name" class="h-10 w-10 rounded-xl ring-2 ring-brand-200 object-cover shadow" />
            <span class="text-xs font-semibold mt-1">{{t.name}}</span>
          </div>
        </ng-container>
      </div>
  <a routerLink="/compare-now" class="btn-primary ml-4">Compare Now</a>
      <button class="btn-secondary ml-2" (click)="clearCompare()">Clear</button>
    </div>
  `
})
export class ToolsComponent {
  private state = signal<{ [key: string]: string }>({});

  constructor(public svc: ToolService) {}

  filtered = computed(() => {
    const s = this.state();
    return this.svc.tools().filter((t: any) => {
      return (!s['category'] || t.category === s['category'])
        && (!s['pricing'] || t.pricing === s['pricing'])
        && (!s['rating'] || t.rating >= Number(s['rating']))
        && (!s['q'] || t.name.toLowerCase().includes(s['q'].toLowerCase()) || t.shortDescription.toLowerCase().includes(s['q'].toLowerCase()));
    });
  });

  compareTools = computed(() => {
    const ids = Array.from(this.svc.compareList());
    return this.svc.tools().filter((t: any) => ids.includes(t.id));
  });

  isCompared(id: string): boolean {
    return this.svc.compareList().has(id);
  }

  clearCompare() {
    this.svc.clearCompare();
  }

  public onFilters(v: { [key: string]: string }) {
    this.state.set(v);
  }
}
